 kenburns-bottom\n * ----------------------------------------\n */\n\n\n\n@keyframes kenburns-bottoms {\n  0% {\n    -webkit-transform: scale(1) translateY(0);\n    transform: scale(1) translateY(0);\n    -webkit-transform-origin: 50% 84%;\n    transform-origin: 50% 84%;\n  }\n  100% {\n    -webkit-transform: scale(1.25) translateY(15px);\n    transform: scale(1.25) translateY(15px);\n    -webkit-transform-origin: bottom;\n    transform-origin: bottom;\n  }\n}\n\n@keyframes slide-in-bottom {\n  0% {\n    -webkit-transform: translateY(1000px);\n    transform: translateY(1000px);\n    opacity: 0\n  }\n  100% {\n    -webkit-transform: translateY(0);\n    transform: translateY(0);\n    opacity: 1\n  }\n}\n\n","html {\n  position: relative;\n  min-height: 100%;\n  height: 100%;\n}\nbody {\n  /* Margin bottom by footer height */\n  margin-bottom: 60px;\n  height: 100%;\n}\n\n\n\n\n\n.page-header {\n  border-bottom: 1px solid $color1;\n  padding-bottom: 5px;\n}\n\n.footer {\n\n  //position:absolute;\n  bottom: 0px;\n  //width: 100%;\n  margin: 0;\n  background-color: #000;\n  height: 100px; /* or however high you would like */\n}\n\n.btn-group {\n  margin-bottom: 30px;\n  padding: 2px;\n\n}\n\n.btn-group button{\n  padding: 1px;\n  border-radius: 5px;\n  color: $color1;\n  background-color: $color2;\n}\n\n.btn-group button:hover, .btn.active{\n  padding: 1px;\n  border-radius: 5px;\n  border-color: $color1;\n  color: white;\n  background-color: $color1;\n  font-weight: bold;\n}\n\n.btn.view.active {\n  padding: 1px;\n  border-radius: 5px;\n  border-color: $color1;\n  color: white;\n  background-color: $color1;\n  font-weight: bold;\n}\n\n\n\n.btn{\n background-color: #e82c75;\n}\n","$black: #303030;\n$width: 800px;\n$purple: #262161;\n$yellow: #FFC40E;\n$pink: #EE7297;\n$green: #93FF00;\n$red: #E50714;\n$grey: lighten(grey, 40%);\n$shad: 0 0 10px rgba(0,0,0,0.1), 0 5px 10px rgba(0,0,0,0.05);\n$grad: linear-gradient(90deg, #48ded4 0%, #a026bf 20%, #e82c75 60%, $yellow 85%, #48ded4 95%);\n$verticalGrad: linear-gradient(0, #48ded4 0%, #a026bf 20%, #e82c75 60%, $yellow 85%, #48ded4 95%);\n$color1: rgb(53, 129, 184);\n$color2: rgb(196, 229, 245);\n$color3: rgb(29, 53, 87);\n$color4: rgb(255, 197, 49);\n$color5: rgb(255, 0, 91);\n\n","\n/*Here the nav section*/\n.img_nav {\n  height: 20px;\n  width: 20px;\n}\n\n.navbar{\n  border: 0;\n  background-color: $color3;\n}\n\nnav > .container-fluid{\n background-color: $color3;\n}\n.navbar-inverse .navbar-collapse, .navbar-inverse .navbar-form,\n.navbar-inverse .navbar-nav > li > a,\n.navbar > .container-fluid .navbar-brand {\n  background-color: $color3;\n  border: none;\n  color: white;\n}\n\n.navbar-inverse .navbar-nav > li > a:hover,\n.navbar-inverse .navbar-nav > li > a:focus {\n  //border-bottom: 2px solid $color4;\n  box-shadow: inset 0 -3px 0 $color1;\n\n}\n\n\n.navbar-inverse .navbar-nav > li > a.active,\n.navbar-inverse .navbar-nav > li > a.active > .svg path{\n  color: white;\n  background: $color1;\n  text-shadow: none;\n\n}\n\n","body {\n  padding: 60px;\n  font-family: 'Abel', sans-serif;\n  font-size: 14px;\n}\n\n\n\np {\n  color: $color3;\n}\n\nh1, h2, h3, h4, h5, h6 {\n  color: $color3;\n}\n\n\n\na.link{\n  color: #000;\n  background-color: white;\n  border-bottom: 2px solid $color4  ;\n  text-decoration: none;\n}\n\n","// CSS for the Candidates page\n.fun_hover {\n  font-size: 2rem;\n\n  background-image: linear-gradient(to right, white 50%, $color1 10%);\n  background-position: 0;\n  // make the background twice as big\n  background-size: 201%;\n  transition: all 0.4s;\n  &:hover {\n    text-decoration: none;\n    color: white;\n    background-position: -100%;\n  }\n\n}\n\n.orgpol {\n  color: $color1;\n}\n\n.row.candidate > .grid {\n  -webkit-transition: all  0.6s cubic-bezier(0.99, 0.53, 0.58, 1.1);\n  -moz-transition: all  0.6s cubic-bezier(0.99, 0.53, 0.58, 1.1);\n  -o-transition: all 0.6s cubic-bezier(0.99, 0.53, 0.58, 1.1);\n  transition: all 0.4s cubic-bezier(0.99, 0.53, 0.58, 1.1);\n  float: left;\n  width: 25%;\n  height: 150px;\n\n  border: 1px solid #f1f1f1;\n  border-radius: 2px;\n  padding: 20px;\n}\n\n.row.candidate > .media {\n  -webkit-transition: all  0.6s ease-out;\n  -moz-transition: all  0.6s ease-out;\n  -o-transition: all 0.6s ease-out;\n  transition: all 0.6s ease-out;\n  border-top: 1px solid #f1f1f1;\n  padding-top: 2px;\n  flex: 1;\n  flex-wrap: wrap;\n\n}\n\n\n//.progress-meter text {\n//  font-family:\"Helvetica Neue\", Helvetica, Arial, sans-serif;\n//  font-size: 24px;\n//  font-weight: bold;\n//}\n\n\ndiv.map > svg{\n\n  height: 100%;\n  width: 100%;\n\n  transform: scale(0.75);\n  //border: solid 1px 50% $color3 ;\n\n}\n\n//This is for the container div of each candidate\n.containerP{\n  display: flex;\n  height: inherit;\n  align-content: space-around;\n  flex-flow: row;\n  margin-bottom: 5px;\n  margin-top: 5px;\n\n  border-top: 1px solid #e9e9e9;\n}\n\n.img-circle{\n  height: 60px;\n  width: 60px;\n}\n\n.itemflex{\n  padding-top: 5px;\n  align-self: center;\n  justify-content: center;\n  text-align: center;\n  flex: 1;\n}\n\n\n\n.barchart{\n  height: 120px;\n  padding-top: 20px;\n}\n\n//This is the final text showing the results\n\n.results{\n  display: flex;\n  flex-direction: column;\n  padding-bottom: 2px;\n  justify-content: space-around;\n  align-content: center;\n\n\n}\n\n.resultsSplit{\n\n  display: flex;\n  height: 100%;\n  border-top:  1px solid $color3;\n\n\n}\n.resultsSplitItem{\n  flex: 1;\n  color: white;\n  font-size: .95em;\n\n\n}\n\n .men > div {\n\n  background-color: $color1;\n\n}\n\n .women{\n   background-color: $color5;\n\n }\n\n\n// Pie Chart\n\n.itemflex >  small{\n\n  color: $color3;\n  font-weight: 200;\n  text-align: center;\n}\n\n\n\n@media (max-width: 200px) {\n  .results {\n    flex-direction: column;\n  }\n .resultsSplit{\n    display: flex;\n  }\n\n  .resultsSplitItem {\n    flex: 1;\n  }\n}","@import \"variables\";\n\nbody {\n  margin: 5px;\n\n\n}\n\n\n\n.bar {\n  fill: $color1;\n}\n.background-bar{\n  fill: $color2 ;\n  opacity: .4;\n}\n\n\n.axis path,\n.axis line {\n  fill: none;\n  stroke: $color3;\n  stroke-width: 0.4px;\n  shape-rendering: crispEdges;\n}\n.axis text{\n  background-color: black;\n}\n\n.x path {\n  display: none;\n}\n.toolTip {\n  position: absolute;\n  display: none;\n  min-width: 80px;\n  height: auto;\n  background: none repeat scroll 0 0 #ffffff;\n  border: 1px solid $color2;\n  padding: 14px;\n  text-align: center;\n\n}\n\n\n\n\npattern{\n  fill: $color1;\n}\n\n\ndiv.media-body > svg.svgEmbed > path {\n  fill: red;\n\n}\n\n\n.barchart text{\n  fill: $color1;\n\n  font-size: .8em;\n  text-anchor: middle;\n  text-align: center;\n}\n\n\n.barchart .bartext{\n  fill: $color1;\n\n  font-size: .95em;\n  text-anchor: middle;\n}\n\n.pieText{\n  color: #fff200;\n  background-color: #fff200;\n} \n\n\n\n\n\n","\n.table>thead>tr>th {\n  cursor: pointer;\n  border-bottom: 1px solid $color3;\n  color: $color1;\n}\n\n.table>thead>tr>th > span > img.svg {\n  padding-bottom: 2px;\n\n}\n\n\n.surveyTitle{\n  padding-left: 20px;\n  border-bottom: 1px solid $color3;\n}\n\ndl.row {\n\n  margin: 5px;\n  margin-top: 15px;\n}\n\ndl.row > dd> a {\n  color: #000;\n  background-color: white;\n  border-bottom: 2px solid $color4  ;\n  text-decoration: none;\n}","/* \r\nMax width before this PARTICULAR table gets nasty\r\nThis query will take effect for any screen smaller than 760px\r\nand also iPads specifically.\r\n*/\r\ntable {\r\n    background-color: aqua;\r\n}\r\n\r\n@media only screen and (max-width: 760px),\r\n(min-device-width: 768px) and (max-device-width: 1024px) {\r\n\r\n    /* Force table to not be like tables anymore */\r\n    table,\r\n    thead,\r\n    tbody,\r\n    th,\r\n    td,\r\n    tr {\r\n        display: block;\r\n    }\r\n\r\n    /* Hide table headers (but not display: none;, for accessibility) */\r\n    thead tr {\r\n        position: absolute;\r\n        top: -9999px;\r\n        left: -9999px;\r\n    }\r\n\r\n    tr {\r\n        border: 1px solid #ccc;\r\n    }\r\n\r\n    td {\r\n        /* Behave  like a \"row\" */\r\n        border: none;\r\n        border-bottom: 1px solid #eee;\r\n        position: relative;\r\n        padding-left: 50%;\r\n    }\r\n\r\n    td:before {\r\n        /* Now like a table header */\r\n        position: absolute;\r\n        /* Top/left values mimic padding */\r\n        top: 6px;\r\n        left: 6px;\r\n        width: 45%;\r\n        padding-right: 10px;\r\n        white-space: nowrap;\r\n    }\r\n\r\n    /*\r\n\tLabel the data\r\n\t*/\r\n    td:nth-of-type(1):before {\r\n        content: \"Candidate\";\r\n    }\r\n\r\n    td:nth-of-type(2):before {\r\n        content: \"Total\";\r\n    }\r\n\r\n    td:nth-of-type(3):before {\r\n        content: \"Men\";\r\n    }\r\n\r\n    td:nth-of-type(4):before {\r\n        content: \"Women\";\r\n    }\r\n}"],"sourceRoot":""}]);

